/*
  elm-hot creates an additional div wrapper around the app to make HMR possible.
  This could break styling in development mode if you are using Elm UI.

  More context in the issue:
    https://github.com/halfzebra/create-elm-app/issues/320
*/
[data-elm-hot="true"] {
  height: inherit;
}

body {
    font-family: 'Rubik', sans-serif;
    height: 100%;
}

html {
    height: 100%;
}
.favbtn {
    cursor: pointer;
    font-size: 14pt;
}

.hour {
    margin-top: 2em;
    width: 100%;
    border-bottom: 2px solid black;
    margin-bottom: 10px;
}

.hour h2 {
    margin-bottom: -5px;
}

.selector {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    background: rgb(2,0,36);
    background: linear-gradient(180deg, rgba(2,0,36,0.40) 0%, rgba(121,9,121,0.40) 90%);
    padding: 10px 5px 10px 5px;
}

.selector a {
    margin-left: 10px;
}
.selector a {
	box-shadow:inset 0px 1px 0px 0px #d9fbbe;
	background:linear-gradient(to bottom, #b8e356 5%, #a5cc52 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#b8e356', endColorstr='#a5cc52',GradientType=0);
	background-color:#b8e356;
	border-radius:3px;
	border:1px solid #83c41a;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:14px;
	font-weight:bold;
	padding:6px 24px;
	text-decoration:none;
	text-shadow:0px 1px 0px #86ae47;
}
.selector a:hover {
	background:linear-gradient(to bottom, #a5cc52 5%, #b8e356 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#a5cc52', endColorstr='#b8e356',GradientType=0);
	background-color:#a5cc52;
}
.selector a:active {
	position:relative;
	top:1px;
}

.search {
    margin: 0 5px 0 10px;
}
.main {
    margin-bottom: 50px;
}
@media only screen and (max-width: 600px) {
    .main {
        margin-top: 80px;
    }
    .search {
        width: 90px;
    }
    .left {
        margin-left: 10px;
    }
    .right {
        margin-left: 10px;
        margin-top: 10px;
    }
}
.selector span {
    -webkit-column-break-inside: avoid;
       -moz-column-break-inside: avoid;
            break-inside: avoid;
}

@media only screen and (min-width: 600px) {
    .right {
        float: right;
        margin-right: 40px;
    }
    .left {
        float: left;
        margin-left: 30px;
    }
    .main {
        margin-top: 70px;
    }
}

/*input {
    margin-left: 20px;
}*/
.selector a {
}

.sel-days button {
    padding: 5px 10px;
}

.events {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fill, minmax(340px,1fr));
    grid-auto-rows: 14px;
}

.colordesc {
    flex-wrap: wrap;
    /* align-self: stretch; */
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    border-radius: 8px;
    padding: 10px 16px 10px 16px;
    margin: 10px;
    max-width: 640px;
}


.event {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    border-radius: 8px;
    padding: 10px 16px 10px 16px;
    margin: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    width: 100%;
}

.title {
    font-size: 14pt;
    font-weight: 800;
}

.camp {
    font-size: 12pt;
    font-weight: 800;
}

.host {
    font-size: 10pt;
    font-family: 'Roboto', sans-serif;
}

.time {
    font-size: 10pt;
    font-family: 'Roboto', sans-serif;
}

.day {
    font-size: 10pt;
    font-family: 'Roboto', sans-serif;
    padding-left: 10px;
}

.days {
    display: flex;
    justify-content: flex-end;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.description {
    font-family: 'Roboto', sans-serif;
    font-size: 12pt;
    font-weight: 400;
}

@font-face {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 400;
    src: local('Rubik'), local('Rubik-Regular'),
         url('fonts/rubik-v8-latin-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('fonts/rubik-v8-latin-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: local('Roboto'), local('Roboto-Regular'),
         url('fonts/roboto-v19-latin-regular.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('fonts/roboto-v19-latin-regular.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

.camplist {
    font-size: 10pt;
    -webkit-columns: 28rem auto;
       -moz-columns: 28rem auto;
            columns: 28rem auto;
    list-style-type: none;
}
.camplocation {
    float: right;
    padding-right: 30px;
}

.map {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 90%;
}

.wall {
    height: calc(100% - 90px);
}

.iframe-container {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    margin: 0;
    padding: 0;
}

.iframe-embed {
    flex-grow: 1;
    border: none;
    margin: 0;
    padding: 0;
    height: 100%;
}

#innerdocbody, #outerdocbody {
    background-color: #fff !important;
}
